import React, { Component, Fragment } from 'react';
import axios from 'axios';
import tumblr from '../imagenes/tumblr.png';
import twitter from '../imagenes/twitter.png';
import './frases.css';
import { DataFrases } from '../server/dataFrases.js'

class Frases extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dataFrases: [],
            number: 0,
            numA: 0,
            color: ["#f44336", "#e91e63", "#9c27b0", "#673ab7", "#3f51b5", "#2196f3", "#03a9f4", "#00bcd4", "#009688", "#4caf50", "#8bc34a", "#cddc39", "#ffeb3b", "#ffc107", "#ff9800", "#ff5722", "#795548", "#607d8b"],
            autor: 'Henry Ford'
        }
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick = () => {
        this.setState(() => ({
            number: Math.floor((Math.random() * (5 - 0)) + 0),
            numA: Math.floor(((Math.random() * (18 - 0)) + 0)),
        }))
    }
    render() {
        const { numA } = this.state
        return (
            <Fragment>
                <body style={{backgroundColor: this.state.color[numA] }}></body>
                <div className="container-fluid d-flex justify-content-center align-item-center p-3 "
                    style={{ backgroundColor: this.state.color[numA] }}>
                    <div className="card mx-auto mt-4 mb-4">
                        <div className="card-body">
                            <h5 className="card-title mt-1"></h5>
                            <i className="fa fa-quote-left" style={{ color: this.state.color[numA] }} > </i>
                            <span className="card-text" style={{ color: this.state.color[numA] }}>
                                {(DataFrases[this.state.number].frase)}</span>
                            <div className="container">
                                <p>-{(DataFrases[this.state.number].autor)}</p>
                            </div>
                            <div className="row mt-4">
                                <div className="col">
                                    <div className="row">
                                        <div className="col mx-2" style={{ backgroundColor: this.state.color[numA] }}>
                                            <a><i><img width="30" src={tumblr}></img></i></a>
                                        </div>
                                        <br />
                                        <div className="col" style={{ backgroundColor: this.state.color[numA] }} >
                                            <a><i><img width="30" src={twitter}></img></i></a>
                                        </div>
                                    </div>
                                </div>
                                <div className="col">
                                    <button style={{
                                        borderColor: this.state.color[numA],
                                        backgroundColor: this.state.color[numA]
                                    }}
                                        onClick={this.handleClick}> Generar</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
export default Frases;